import React from 'react';
import Image from 'next/image';
import { useTranslation } from 'react-i18next';

interface PortfolioCardProps {
  href: string;
  imageSrc: string;
  title: string;
  client: string;
  location: string;
}

const PortfolioCard: React.FC<PortfolioCardProps> = ({ href, imageSrc, title, client, location }) => {
  const { t } = useTranslation('common');

  return (
    <div className="block bg-white rounded-lg overflow-hidden hover:shadow-xl transition-shadow duration-300 p-0"> {/* Outer div removed */}
      <div className="relative w-full h-64"> {/* Increased height */}
        <Image
          src={imageSrc}
          alt={title}
          fill
          style={{ objectFit: 'cover' }}
          className="absolute inset-0 w-full h-full object-cover" // Ensures image takes full width and height
        />
      </div>
      <div className="p-2">
        <h3 className="text-lg font-semibold mb-1">{title}</h3>
        <p className="text-sm text-gray-500 mb-1">{t('portfolio.projects.client')}: {client}</p>
        <p className="text-sm text-gray-500">{t('portfolio.projects.location')}: {location}</p>
      </div>
    </div>
  );
};

export default PortfolioCard;
